.App {
  text-align: center;
}

a {
  text-decoration-line: none;
  color: white;
}

.navbar:hover {
  opacity: 0.5;
  transition: 2s;
} 

img {
  width: 100%;
}

.container1 img:first-child { 
  height: 100%;
}


.container1 {
  position: relative;
  display: flex;
  flex-wrap: wrap;
}

.item {
  flex: 1;
  flex-basis: 600px;
  position: relative;
  overflow: hidden;
}

.videoTag {
  flex: 1;
  flex-basis: 600px;
  position: relative;
  overflow: hidden;
}

.projects p {
  margin-left: 10%;
  margin-right: 10%;
}

.myProjects {
  flex: 1;
  flex-basis: 600px;
  position: relative;
  overflow: hidden;
  border: 1px solid black;
  margin: 10%;
}

.text-block {
  position: absolute;
  color: white;
  z-index: ;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: 5%; /*Adjust this value to move the positioned div up and down */
  /* width: 60%; Set the width of the positioned div */
}

.aboutme {
  background-color: #1B2326;
  padding-top: 80px;
  color: white;
  position: relative;
}

.aboutme p {
  width: 80%;
  margin: auto;
}

.skills {
  padding-top: 80px;
}

.projects {
  padding-top: 80px;
}

video {
  width: 100%;
}

.container2 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
}

.logo {
  width: 30%;
  margin: 0 auto;
  padding: 10px;
}

.projectImg {
  width: 100%;
  margin: 0 auto;
}

.me {
  border-radius: 50%;
  width: 30%;
  margin: 0 auto;
  padding: 10px;
}

