.navbar {
 /* margin: 1rem;*/
  justify-content: space-between;
  background-color: #1B2326;
  color: white;
  position: sticky;
  top: 0;
  z-index: 1;
}

.overlay {
  transition: bottom ease 1s;
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: absolute;
  bottom: -50px;
  left: 0;
  width: 100%;
  background-color:rgb(255, 255, 255, 0.5);

}

.item:hover .overlay {
  bottom: 0;
}

.overlap {
  transition: bottom linear 1s;
  position: absolute;
  bottom: -300px;
  left: 0;
  width: 100%;
  background-color:rgb(0, 0, 0, 0.7);
}

.myProjects:hover .overlap {
  bottom: 0;
  height: 100%;
  color: white;
}


h1, h2, h3, h4, h5, h6 {
	margin: 0;
	padding: 0;
	border: 0;
}



@media only screen and (min-width: 600px) {
  
  .navbar {
    display: flex;
    /*margin: 1rem;*/
    justify-content: space-between;
    background-color: #1B2326;
    color: white;
  }
  .portfolio h2 {
    color: white;
    padding: 0 1rem;
  }
  .nav{
    display: inline-flex;
    align-items: center;
  }
  .nav h4{
    color: white;
    padding: 0 2rem;
  }
  .container3 {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }
  .aboutme, .skills, .projects {
    padding-top: 28px;
  }
  
}